@import "../../scss/Variables.scss";

.slider {
  padding: $base * 16 0 $base * 20 0;
  background-color: $color-light-l;

  .h3 {
    text-align: center;
  }

  .button__primary {
    display: block;
    width: $base * 50;
    margin: 0 auto;
  }
}

.slider__container {
  display: flex;
  justify-content: space-between;
  gap: $base * 18;
  width: 77%;
  margin: $base * 12 auto;
  overflow-x: hidden;
}

.slider__card {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: $base * 6;
  background-color: $color-light-s;
}

.slider__img {
  // width: 100%;
  height: auto;
}
